import React from 'react';
import { Link } from 'react-router-dom';
import { Campaign } from '../../../types/campaign';
import { CampaignStatusBadge } from '../campaigns/CampaignStatusBadge';

interface RecentCampaignsProps {
    campaigns: Campaign[];
}

export const RecentCampaigns: React.FC<RecentCampaignsProps> = ({ campaigns }) => {
    return (
        <div>
            <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-semibold text-gray-900">Recent Campaigns</h3>
                <Link to="/campaigns" className="text-sm text-blue-600 hover:text-blue-700 font-medium">
                    View all
                </Link>
            </div>
            <div className="space-y-3">
                {campaigns.length === 0 ? (
                    <div className="bg-white rounded-lg border border-gray-200 p-8 text-center">
                        <p className="text-gray-500">No campaigns yet</p>
                    </div>
                ) : (
                    campaigns.map((campaign) => (
                        <div key={campaign.id} className="bg-white rounded-lg p-4 border border-gray-200 hover:shadow-sm transition-shadow">
                            <div className="flex items-center justify-between mb-2">
                                <h4 className="font-medium text-gray-900">{campaign.name}</h4>
                                <CampaignStatusBadge status={campaign.status} />
                            </div>
                            <div className="flex items-center justify-between text-sm text-gray-600">
                                <span>Sent: {campaign.sent}</span>
                                <span>Delivered: {campaign.delivered}</span>
                            </div>
                        </div>
                    ))
                )}
            </div>
        </div>
    );
};
