import React, { useEffect } from 'react';
import { useCampaignStore } from '../../services/campaign';
import { useTemplateStore } from '../../services/template';
import { DashboardStats } from './DashboardStats';
import { QuickActions } from './QuickActions';
import { RecentCampaigns } from './RecentCampaigns';
import { RecentTemplates } from './RecentTemplates';

const Dashboard: React.FC = () => {
    const { loadTemplates, templates } = useTemplateStore();
    const { loadCampaigns, campaigns } = useCampaignStore();

    useEffect(() => {
        loadTemplates();
        loadCampaigns();
    }, []);

    return (
        <div className="space-y-8">
            <div>
                <h2 className="text-2xl font-bold text-gray-900">Dashboard</h2>
                <p className="text-gray-600 mt-1">Overview of your marketing campaigns</p>
            </div>

            <DashboardStats templates={templates} campaigns={campaigns} />

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                <RecentTemplates templates={templates.slice(0, 3)} />
                <RecentCampaigns campaigns={campaigns.slice(0, 3)} />
            </div>

            <QuickActions />
        </div>
    );
};

export default Dashboard;
