import React from 'react';
import { Link } from 'react-router-dom';
import { Template } from '../../../types/template';
import { TemplateStatusBadge } from '../templates/TemplateStatusBadge';

interface RecentTemplatesProps {
    templates: Template[];
}

export const RecentTemplates: React.FC<RecentTemplatesProps> = ({ templates }) => {
    return (
        <div>
            <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-semibold text-gray-900">Recent Templates</h3>
                <Link to="/templates" className="text-sm text-blue-600 hover:text-blue-700 font-medium">
                    View all
                </Link>
            </div>
            <div className="space-y-3">
                {templates.length === 0 ? (
                    <div className="bg-white rounded-lg border border-gray-200 p-8 text-center">
                        <p className="text-gray-500">No templates yet</p>
                    </div>
                ) : (
                    templates.map((template) => (
                        <div
                            key={template._id}
                            className="bg-white rounded-lg p-4 border border-gray-200 hover:shadow-sm transition-shadow"
                        >
                            <div className="flex items-center justify-between mb-2">
                                <h4 className="font-medium text-gray-900">{template.displayName}</h4>
                                <TemplateStatusBadge status={template.status} />
                            </div>
                            <div className="flex items-center justify-between text-sm text-gray-600">
                                <span className="uppercase">{template.type}</span>
                                <span>Used {template.usageCount} times</span>
                            </div>
                        </div>
                    ))
                )}
            </div>
        </div>
    );
};
