import classNames from 'classnames';
import React from 'react';

interface StatCardProps {
    title: string;
    value: string | number;
    subtitle?: string;
    trend?: number;
    className?: string;
}

export const StatCard: React.FC<StatCardProps> = ({ title, value, subtitle, trend, className }) => {
    return (
        <div className={classNames('bg-white rounded-lg shadow-sm border border-gray-200 p-6', className)}>
            <p className="text-sm font-medium text-gray-600">{title}</p>
            <p className="text-2xl font-bold text-gray-900 mt-2">{value}</p>
            <div className="flex items-center space-x-2 mt-2">
                {subtitle && <p className="text-xs text-gray-500">{subtitle}</p>}
                {trend !== undefined && (
                    <span
                        className={classNames(
                            'text-xs px-2 py-1 rounded-full font-medium',
                            trend > 0 ? 'bg-green-100 text-green-700' : trend < 0 ? 'bg-red-100 text-red-700' : 'bg-gray-100 text-gray-700'
                        )}
                    >
                        {trend > 0 ? '+' : ''}
                        {trend}%
                    </span>
                )}
            </div>
        </div>
    );
};
