import React from 'react';
import { Campaign } from '../../../types/campaign';
import { Template } from '../../../types/template';
import { StatCard } from '../../components/StatCard';

interface DashboardStatsProps {
    templates: Template[];
    campaigns: Campaign[];
}

export const DashboardStats: React.FC<DashboardStatsProps> = ({ templates, campaigns }) => {
    const approvedTemplates = templates.filter((t) => t.status === 'approved').length;
    const activeCampaigns = campaigns.filter((c) => c.status === 'sending').length;

    const totalDelivered = campaigns.reduce((sum, c) => sum + c.delivered, 0);
    const totalSent = campaigns.reduce((sum, c) => sum + c.sent, 0);
    const deliveryRate = totalSent > 0 ? ((totalDelivered / totalSent) * 100).toFixed(1) : '0';

    return (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            <StatCard title="Total Templates" value={templates.length} subtitle={`${approvedTemplates} approved`} trend={12} />
            <StatCard title="Active Campaigns" value={activeCampaigns} subtitle={`${campaigns.length} total`} trend={8} />
            <StatCard title="Messages Sent" value={totalSent.toLocaleString()} subtitle="All time" trend={15} />
            <StatCard title="Delivery Rate" value={`${deliveryRate}%`} subtitle="Last 30 days" trend={-2} />
        </div>
    );
};
