import React from 'react';
import { useNavigate } from 'react-router-dom';

export const QuickActions: React.FC = () => {
    const navigate = useNavigate();

    const actions = [
        {
            title: 'Create Template',
            description: 'Build a new message template',
            color: 'blue',
            onClick: () => navigate('/templates/create'),
        },
        {
            title: 'Launch Campaign',
            description: 'Start a new marketing campaign',
            color: 'green',
            onClick: () => navigate('/campaigns/create'),
        },
        {
            title: 'Add Integration',
            description: 'Connect a new channel',
            color: 'purple',
            onClick: () => navigate('/integrations/create'),
        },
    ];

    return (
        <div>
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Quick Actions</h3>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                {actions.map((action) => (
                    <button
                        key={action.title}
                        onClick={action.onClick}
                        className={`bg-${action.color}-50 border border-${action.color}-200 rounded-lg p-6 text-left hover:bg-${action.color}-100 transition-colors`}
                    >
                        <h4 className="font-medium text-gray-900 mb-1">{action.title}</h4>
                        <p className="text-sm text-gray-600">{action.description}</p>
                    </button>
                ))}
            </div>
        </div>
    );
};
